.projects__title {
  display: flex;
  justify-content: center;
}

.right__button,
.left__button {
  background-color: #fff;
  cursor: pointer;
  padding: 0;
  border: none;
}

.right__button_type_not-active,
.left__button_type_not-active {
  visibility: hidden;
}

.projects__container {
  display: flex;
  flex-direction: row;
}

.button__icon {
  width: 50px;
  height: 50px;
  border: 5px inset deeppink;
  border-radius: 100%;
}

.button__icon:hover {
  opacity: 0.5;
}

@media screen and (max-width: 450px) {
  .button__icon {
    width: 20px;
    height: 20px;
    border: 2px inset deeppink;
    border-radius: 100%;
  }
}
